  height: 78vh;
}
#form_signup {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  justify-content: center;
  padding: 3%;
  /* background-color: aqua; */
}
.one-by-two {
  display: flex;
}

.button-component {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  align-content: center;
}

@media (max-width: 1018px) {
  .signup {
    height: auto;
  }
  .signup-box {
    flex-direction: column;
    width: 100%;
  }
  #form_signup {
    width: 100%;
  }
  .signup-img img {
    border-bottom-left-radius: 20px;
    border-top-right-radius: 0;
    width: 100%;
  }
}

@media (max-width: 587px) {
  .artlogo {
    width: 100%;
    height: 10vh;
  }
}

.formik-login-form {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1%;
  flex-direction: column;
}

.modal-title{
    display:flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: row;
}
.modal-title span{ 
    margin-left: 3px;
    margin-top: -10px;
}


